<template>
  <div>
    <Loading :active="isLoading" :z-index="1060"></Loading>
    <div class="text-end mt-4">
      <button class="btn btn-primary" type="button" @click="openModal(true)">
        
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th style="width: 200px"></th>
          <th style="width: 200px"></th>
          <th></th>
          <th style="width: 100px"></th>
          <th style="width: 100px"></th>
          <th style="width: 120px"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="article in articles" :key="article.id">
          <td>{{ article.title }}</td>
          <td>{{ article.author }}</td>
          <td>{{ article.description }}</td>
          <td>{{ $filters.date(article.create_at) }}</td>
          <td>
            <span v-if="article.isPublic"></span>
            <span v-else></span>
          </td>
          <td>
            <div class="btn-group">
              <button
                class="btn btn-outline-primary btn-sm"
                type="button"
                @click="getArticle(article.id)"
              >
                
              </button>
              <button
                class="btn btn-outline-danger btn-sm"
                type="button"
                @click="openDelArticleModal(article)"
              >
                
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination :pages="pagination" @emit-pages="getArticles"></Pagination>

    <ArticleModal
      ref="articleModal"
      :article="tempArticle"
      :is-new="isNew"
      @add-article="addArticle"
      @edit-article="editArticle"
    ></ArticleModal>

    <DelModal :item="tempArticle" ref="delModal" @del-item="delArticle" />
  </div>
</template>

<script>
import ArticleModal from '@/components/ArticleModal.vue';
import DelModal from '@/components/DelModal.vue';
import Pagination from '@/components/Pagination.vue';

export default {
  data() {
    return {
      articles: [],
      isLoading: false,
      isNew: false,
      tempArticle: {},
      pagination: {},
      currentPage: 1,
    };
  },
  components: {
    ArticleModal,
    DelModal,
    Pagination,
  },
  methods: {
    getArticles(page = 1) {
      this.isLoading = true;

      this.currentPage = page;
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/articles?page=${page}`;
      this.$http
        .get(api)
        .then((result) => {
          if (result.data.success) {
            this.articles = result.data.articles;
            this.pagination = result.data.pagination;
          }
        })
        .catch((error) => {
          this.$httpMessageState(error.response, '');
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    getArticle(id) {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/article/${id}`;
      this.isLoading = true;
      this.$http
        .get(api)
        .then((response) => {
          this.isLoading = false;
          if (response.data.success) {
            this.openModal(false, response.data.article);
            this.isNew = false;
          }
        })
        .catch((error) => {
          // axios https://github.com/axios/axios#handling-errors
          console.log('error', error.response, error.request, error.message);
          this.isLoading = false;
        });
    },
    openModal(isNew, item) {
      this.isNew = isNew;

      // 
      if (isNew) {
        this.tempArticle = {
          isPublic: false,
          create_at: new Date().getTime() / 1000,
          tag: [],
        };
      }
      // 
      else {
        this.tempArticle = { ...item };
      }

      const updateComponent = this.$refs.articleModal;
      updateComponent.openModal();
    },
    addArticle(item) {
      this.isLoading = true;

      this.tempArticle = item;
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/article`;
      this.$http
        .post(api, { data: this.tempArticle })
        .then((result) => {
          this.$httpMessageState(result, '');

          const articleComponent = this.$refs.articleModal;
          articleComponent.hideModal();
        })
        .catch((error) => {
          this.$httpMessageState(error.response, '');
        })
        .finally(() => {
          this.getArticles(this.currentPage);

          this.isLoading = false;
        });
    },
    editArticle(item) {
      this.isLoading = true;

      this.tempArticle = item;
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/article/${this.tempArticle.id}`;
      this.$http
        .put(api, { data: this.tempArticle })
        .then((result) => {
          this.$httpMessageState(result, '');

          const articleComponent = this.$refs.articleModal;
          articleComponent.hideModal();
        })
        .catch((error) => {
          this.$httpMessageState(error.response, '');
        })
        .finally(() => {
          this.getArticles(this.currentPage);

          this.isLoading = false;
        });
    },
    updateArticle(item) {
      this.tempArticle = item;
      let api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/article`;
      let httpMethod = 'post';
      let status = '';
      this.isLoading = true;
      if (!this.isNew) {
        api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/article/${this.tempArticle.id}`;
        httpMethod = 'put';
        status = '';
      }
      const articleComponent = this.$refs.articleModal;
      this.$http[httpMethod](api, { data: this.tempArticle })
        .then((response) => {
          this.isLoading = false;
          this.$httpMessageState(response, status);
          articleComponent.hideModal();
          this.getArticles(this.currentPage);
        })
        .catch((error) => {
          this.isLoading = false;
          this.$httpMessageState(error.response, '');
        });
    },
    openDelArticleModal(item) {
      this.tempArticle = { ...item };

      const delComponent = this.$refs.delModal;
      delComponent.openModal();
    },
    delArticle() {
      this.isLoading = true;

      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/article/${this.tempArticle.id}`;
      this.$http
        .delete(url)
        .then((response) => {
          this.$httpMessageState(response, '');

          const delComponent = this.$refs.delModal;
          delComponent.hideModal();
        })
        .catch((error) => {
          this.$httpMessageState(error.response, '');
        })
        .finally(() => {
          this.getArticles(this.currentPage);

          this.isLoading = false;
        });
    },
  },
  created() {
    this.getArticles();
  },
};
</script>
